/*NavBar and Dropdown CSS begins*/

/*Mynavbar is a long container div with 3 columns, first for logo and dropdown, second blank, third for authlinks*/
.Mynavbar {
  display: grid;
  background-color:#fff;
  grid-template-columns: 1fr 8fr 1fr 1fr;
  justify-content: center;
  padding: 30px 0px;
  grid-column-gap: 0px;
}

/*the container div for the actual logo symbol*/
.logocontainer {
  position: relative;
  grid-column: 1;
  bottom: 20px;
}

.logo {
  margin-top: 10px;
  height: 60px;
  width: 60px;
}

.logo:hover {
  cursor: pointer;
}


.dropbar {
  width:10px;
  position:relative;
  grid-column: 3;
  left:90px;
  color: #1B6EB0;
}


.hamburger {
  position:relative;
  left:40px;
  grid-column: 4;
  width:10px;

}



/*navbar ends*/





